.catalog__item{
  width: 160px;
  text-align: center;
}
.catalog__image{
  background-color: #FFE17F;
  margin-bottom: 33px;
}
.catalog .catalog__title{
  font-family: 'Raleway';
  font-weight: 800;
  line-height: 24px;
  letter-spacing: -0.2%;
  font-size: 18px;
  color: #000;
  text-align: left;
}
.catalog__list{
  width: 1160px;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

